import React, { Component } from 'react';
import './App.css';

import Header from './Header';
import Footer from './Footer';
import Module from './Module';
import Navigation from '../components/Navigation/Navigation';

import Modules from '../site/Modules';
import { cssFactory } from '../common/css';

const css = cssFactory('app');

const getModuleFromHistory = state => (state && state.moduleid) ? state.moduleid : Modules.default;

// A Map moduleId -> scrollTop. push/Popstate would be slicker, but popstate does not give previous state.
const scrollHeights = {};

/* disable buggy scroll-restoration: auto behaviour: when body.overflow is 'hidden', it will stop working whenever a non-overflowing page is hit.
   Therefore we hereby declare that we want to handle scroll restoration ourselves (see pushState calls)
   see also https://developers.google.com/web/updates/2015/09/history-api-scroll-restoration
*/
if ('scrollRestoration' in window.history)
  window.history.scrollRestoration = 'manual';
class App extends Component {
  constructor(props) {
    super(props);
    for (const fn of ['switchModule'])
      this[fn] = this[fn].bind(this);
    /* . */
    this.state = {
      activeModule: getModuleFromHistory(window.history.state)
    };
    window.addEventListener('popstate', event => {
      scrollHeights[this.state.activeModule] = this.rootElement.scrollTop;
      this.setState({ activeModule: getModuleFromHistory(event.state) }, () => {
        this.restoreScroll();
      });
    });
  }
  componentDidMount() {
    this.rootElement = document.getElementById('root');
    this.restoreScroll();
  }
  restoreScroll() {
    const { activeModule } = this.state;
    if (activeModule in scrollHeights)
      this.rootElement.scrollTop = scrollHeights[activeModule];
  }
  switchModule(e) {
    const { moduleid } = e.currentTarget.dataset,
      { scrollTop } = this.rootElement;
    scrollHeights[this.state.activeModule] = scrollTop;
    window.history.pushState({ moduleid }, moduleid);
    this.setState({ activeModule: moduleid });
  }
  render() {
    const module = this.getCurrentModule();
    return (
      <div className={css('main')}>
        <Header />
        <Navigation active={this.state.activeModule} onSwitch={this.switchModule} />
        <Module title={module.title}>
          {module}
        </Module>
        <div className={css('footer')}>
          <Footer onSwitchModule={this.switchModule} />
        </div>
      </div>
    );
  }
  getCurrentModule() {
    return React.createElement(
      Modules[this.state.activeModule].reactClazz, { onSwitch: this.switchModule }
    );
    // switch (this.state.activeModule) {
    //   case 'Home':
    //     return <Home />
    //   case 'Kurse':
    //     return <Kurse />
    //   default:
    //     throw new Error('Module not found.');
    // }
  }
}

export default App;
