import React from 'react';
import './section.css';
import { cssFactory, siteCss } from '../common/css';

const css = cssFactory('section');

/* NOTE_1: Angabe nach §5 TMG ("Impressumspflicht") nötig (bei Umsatzsteuer (kein 'Kleinunternehmer') auch Umsatzsteuer-ID!)
 * https://www.bmjv.de/DE/Verbraucherportal/DigitalesTelekommunikation/Impressumspflicht/Impressumspflicht_node.html
 */
function Datenschutz() {
  return (
    <div className={siteCss('small')}>
      <div className={css('heading')}>
        Datenschutzerklärung
      </div>
      <div className={siteCss('par')}>
        Wenn Sie die Angebote der Internetseite deboradifonzo.de bzw. der freiberuflichen
  Lehrerin Chiara Debora Difonzo (im folgenden „Kursanbieterin“ genannt) nutzen,
  werden personenbezogene Daten unter Beachtung der deutschen und der europäischen
  Datenschutzgesetze verarbeitet.
  </div>
      <div className={siteCss('par')}>
        Die Erhebung, Speicherung und Verarbeitung personenbezogener Daten erfolgt gemäß
        dem Saarländischen Gesetz zum Schutz personenbezogener Daten (Saarländisches
        Datenschutzgesetz - SDSG), der europäischen Datenschutz-Grundsatzverordnung
        (DSGVO) sowie dem Telemediengesetz (TMG). Die Kursanbieterin sowie ihre
        MitarbeiterInnen sind zur Verschwiegenheit und zur Einhaltung der
        Datenschutzbestimmungen verpflichtet worden.
  </div>
      <div className={siteCss('par')}>
        Die Kursanbieterin erhebt zur vertragliche Abwicklung von Sprachkursen (Anmeldung,
        Durchführung, Abrechnung), Einstufungen personenbezogenen Daten und verarbeitet
        diese.
  </div>
      <div className={siteCss('par')}>
        Die Kursanbieterin verarbeitet Ihre Daten gemäß dem Saarländischen Gesetz zum Schutz
        personenbezogener Daten (Saarländisches Datenschutzgesetz - SDSG): Es handelt sich
        um Verarbeitungsvorgänge, die für die Erbringung der genannten Leistungen notwendig
        sind oder um Verarbeitungsvorgänge, die zur Durchführung vorvertraglicher
        Maßnahmen erforderlich sind.
  </div>
      <div className={siteCss('par')}>
        Aufgrund der gesetzlich vorgeschriebenen bzw. zugelassenen oder durch Ihre
        Einwilligung legitimierten Datenerhebung ist die Verarbeitung Ihrer personenbezogenen
        Daten rechtmäßig.
      </div>
    </div>
  );
}

export default Datenschutz;
