import { createElement as $ } from 'react';
import { cssFactory, siteCss, join } from '../common/css';

import bandiere from '../svg/bandiere.svg';
import stemma from '../svg/stemma.svg';
const css = cssFactory('app');

function Header() {
    return (
        $('div', { className: join(css('heading', 'banner-container'), siteCss('relative')) }, [
            $('div', { key: 'A', className: join(css('heading-title'), siteCss('table')) }, [
                $('span', { key: 'A', className: siteCss('tcell', 'of-2', 'justify') }, "Italienisch"),
                $('span', { key: 'B', className: siteCss('tcell', 'of-2', 'justify') }, "Lernen")
            ]),
            $('object', { key: 'B', data: bandiere, type: 'image/svg+xml', className: css('banner') },
                /* $('img', { src: 'yourfallback.jpg' }) */
            )
        ])
    );
}

export default Header;
