import { createElement as $ } from 'react';

import { cssFactory, siteCss } from '../../common/css';

/** *PROPS*
 *  * `moduleid`: one of the keys in `Modules`
 *  * `onFollow`: callback
 */
function ModuleLink(props) {
    return $('span', {
        className: siteCss('internal-link'),
        'data-moduleid': props.moduleid,
        onClick: props.onFollow,
        tabIndex: 0,
        onKeyPress(e) {
            if (e.key == 'Enter')
                props.onFollow(e);
        }
    },
        props.children
    );
}

export default ModuleLink;
