import React from 'react';
import './section.css';
import { cssFactory, siteCss } from '../common/css';
import programmAsHtmlTable from '../site/Programm/programm2react';
import { email, telefon, sprechzeiten } from '../site/data';
const css = cssFactory('section');

function Programm(props) {
  return (
    <div>
      <div className={css('heading')}>
        Programm {2018}
      </div>
      <div className={siteCss('par')}>
        Die Kurse finden an unserem Sitz in der Hafenstraße in Saarbrücken statt (direkt links neben dem Haupteingang des Mercure-Hotels, Nähe Kongresshalle und Europa-Gallerie).
      </div>
      <div className={siteCss('par')}>
        <div className={css('sub-heading')}>Termine</div>
        {programmAsHtmlTable}
      </div>
      <div className={siteCss('par')}>
        Außerdem können auf Vereinbarung <span className={siteCss('bold')} >auch vormittags</span> Kurse angeboten werden!
      </div>
      <div className={siteCss('par')}>
        Der Kursbeginn und die genauen Termine werden während des Anmeldevorgangs vereinbart und dabei auf die Bedürfnisse der Kursteilnehmer angepasst.
      </div>
      <div className={siteCss('par')}>
        <div className={css('sub-heading')}>Voranmeldung</div>
        Wenn Sie teilnehmen möchten, melden Sie sich <em>unverbindlich</em> an,
        <ul className={siteCss('offset')}>
          <li>per E-Mail an <a href={`mailto:${email}?subject=Voranmeldung`}>{email}</a></li>
          <li>
            telefonisch unter {telefon}
            <br />
            (Sprechzeiten: {sprechzeiten})
          </li>
          <li>oder persönlich nach Vereinbarung</li>
        </ul>
        Geben Sie dabei bitte das Kursniveau und die gewünschte Uhrzeit an und, wann Sie anfangen möchten.
      </div>
      <div className={siteCss('par')}>
        <div className={css('sub-heading')}>Anmeldung</div>
        Nach dem ersten Termin werden die Anmeldungen durchgeführt.
        Sollte sich dabei ergeben, dass der Kurs nicht stattfindet, fallen für Sie keinerlei Kosten an.
      </div>
    </div>
  );
}

export default Programm;

