import React, { createElement as $ } from 'react';
import { siteCss, cssFactory } from '../common/css';

import { addresse, telefon, email } from '../site/data';

import '../common/icon.css';
import Modules from '../site/Modules';
import ModuleLink from '../components/Links/ModuleLink';
const css = cssFactory('icon');

function Footer(props) {
    const legalModuleLinks = Modules.legalModules.map(id =>
        $('div', {
            key: id,
            className: siteCss('permanent')
        },
            $(ModuleLink, {
                moduleid: id,
                onFollow: props.onSwitchModule
            },
                Modules[id].desc
            )
        )
    );
    return (
        <div className={siteCss('stretch')}>
            <div className={siteCss('table', 'inline', 'of-2')}>
                <div className={css('icon', 'as-trow', 'copyright')}>
                    <span>Italienisch Lernen {'\u2013'} Debora Difonzo</span>
                </div>
                <div className={css('icon', 'as-trow', 'home')}><span>{addresse.strasse}<br />{addresse.ort}</span></div>
                <div className={css('icon', 'as-trow', 'telephone')}><span>{telefon}</span></div>
                <div className={css('icon', 'as-trow', 'email')}><span>{email}</span></div>
            </div>
            <div className={siteCss('inline', 'of-2')}>
                {legalModuleLinks}
            </div>
        </div>
    );
}

export default Footer;
