import { createElement as $ } from 'react';

import './Nav.css';
import Modules from '../../site/Modules';
import { cssFactory } from '../../common/css';

const css = cssFactory('nav');

/** *PROPS*
 *  * `active`: one of the keys in `Modules`
 *  * `onSwitch`: callback
 */
function Navigation(props) {
    return $('nav', { className: css('bar') },
        Array.from(Modules).map( id => {
            const active = props.active == id;
            return $('span', {
                key: id,
                onClick: props.onSwitch,
                'data-moduleid': id,
                className: css('link', active && 'active'),
                tabIndex: active ? -1 : 0,
                onKeyPress(e) {
                    if (e.key == 'Enter')
                        props.onSwitch(e);
                }
            },
                Modules[id].desc
            );
        })
    );
}

export default Navigation;
