import React from 'react';
import './section.css';
import stemma from '../svg/stemma_INLINED.svg';
import { cssFactory, siteCss } from '../common/css';

const css = cssFactory('section');

function Home() {
  return (
    <div className={css('home')}>
      <img src={stemma} className={css('stemma', 'stemma-left')} />
      <img src={stemma} className={css('stemma', 'stemma-right')} />
      <div className={css('heading')}>
        Herzlich willkommen!
      </div>
      <div className={siteCss('par')}>
        Sie möchten Italienisch lernen?
      </div>
      <div className={siteCss('par')}>
        Dann sind Sie hier am richtigen Ort.
      </div>
      <ul className={siteCss('par', 'wrap-left')}>
        <li>Italienisch Lernen für alle</li>
        <li>im Zentrum von Saarbrücken</li>
        <li>auch samstags und sonntags</li>
      </ul>
    </div>
  );
}

export default Home;
