import Home from "../modules/Home";
import Kurse from "../modules/Kurse";
import Kontakt from "../modules/Kontakt";
import Lehrperson from "../modules/Lehrperson";
import Programm from "../modules/Programm";
import Impressum from "../modules/Impressum";
import Datenschutz from "../modules/Datenschutz";

const Modules = {
    'Home': {
        reactClazz: Home,
        desc: "Home"
    },
    'Lehrperson': {
        reactClazz: Lehrperson,
        desc: "Lehrperson"
    },
    'Kurse': {
        reactClazz: Kurse,
        desc: "Kurse"
    },
    'Programm': {
        reactClazz: Programm,
        desc: "Programm"
    },
    'Kontakt': {
        reactClazz: Kontakt,
        desc: "Kontakt"
    },
    'Impressum': {
        reactClazz: Impressum,
        desc: "Impressum"
    },
    'Datenschutz': {
        reactClazz: Datenschutz,
        desc: "Datenschutzerklärung"
    }
};
/* Object.defineProperty(Modules, 'length', {
    enumerable: false,
    value: Object.keys(Modules).length // constant because of `freeze()`
}); */
/* Allows to define an iteration order (--> display order) */
Object.defineProperty(Modules, Symbol.iterator, {
    enumerable: false,
    value: function* () {
        yield 'Home';
        yield 'Lehrperson';
        yield 'Kurse';
        yield 'Programm';
        yield 'Kontakt';
    }
});
Object.defineProperty(Modules, 'default', {
    enumerable: false,
    value: 'Home'
});
const LEGAL_MODULES = ['Impressum', 'Datenschutz'];
// TODO better use built-in `get` ?
Object.defineProperty(Modules, 'legalModules', {
    enumerable: false,
    value: LEGAL_MODULES
});

export default Object.freeze(Modules);
