import React from 'react';
import './section.css';
import { cssFactory, siteCss, join } from '../common/css';

import { addresse, telefon, email, sprechzeiten } from '../site/data';

const css = cssFactory('section'), iconCss=cssFactory('icon');

/* NOTE_1: Angabe nach §5 TMG ("Impressumspflicht") nötig (bei Umsatzsteuer (kein 'Kleinunternehmer') auch Umsatzsteuer-ID!)
 * https://www.bmjv.de/DE/Verbraucherportal/DigitalesTelekommunikation/Impressumspflicht/Impressumspflicht_node.html
 * UPDATE: Jetzt extra: vom Anwalt begutachtetes Impressum und Datenschutzerklärung
 */
function Kontakt() {
  return (
    <div>
      <div className={css('heading')}>
        Kontakt
        {/* <span className={siteCss('footnote-marker')} onClick={e=>window.document.getElementById('Kontakt--footnote-Tmg').scrollIntoView()} /> */}
      </div>
      <div className={siteCss('par')}>
        <div className={css('sub-heading')}><span className={iconCss('icon','home')} />Anschrift</div>
        <ul className={siteCss('offset')}>
          <li>{'Italienisch Lernen \u2013 Debora Difonzo'}</li>
          <li>Chiara Debora Difonzo</li>{/* Vor- und Familienname cfr. NOTE_1 */}
          <li>{addresse.strasse}</li>
          <li>{addresse.ort}</li>
        </ul>
        Persönliche Beratung nach Vereinbarung
      </div>
      <div className={siteCss('par')}>
        <div className={css('sub-heading')}><span className={iconCss('icon','telephone')} />Telefon</div>
        <ul className={siteCss('offset')}>
          <li>{telefon}</li>
          <li>Sprechzeiten: {sprechzeiten}</li>
        </ul>
      </div>
      <div className={siteCss('par')}>
        <div className={css('sub-heading')}><span className={iconCss('icon','email')} />E-Mail</div>
        <ul className={siteCss('offset')}>
          <li><a href={`mailto:${email}`}>{email}</a></li>
        </ul>
      </div>
      {/* <div className={siteCss('footnotes')}>
      <p id='Kontakt--footnote-Tmg' className={siteCss('footnote')}>
        Anbieterkennzeichnung nach §5 TMG
      </p>
      </div> */}
    </div>
  );
}

export default Kontakt;
