import React from 'react';
import './section.css';
import { cssFactory, siteCss } from '../common/css';

import portrait from '../img/portrait.png';
const css = cssFactory('section');

function Lehrperson() {
  return (
    <div>
      <div className={css('heading')}>
        Italienisch Lernen
      </div>
      <div className={siteCss('par', 'has-float')}>
        <img src={portrait} alt="Portrait" style={{ float: 'right', width: '20%', minWidth: '6em', marginLeft: '1em' }} />
        Die Italiensch&shy;kurse werden von der Mutter&shy;sprachlerin Debora Difonzo geleitet.
        <br />
        Sie ist seit 2002 in Deutschland als Italienisch&shy;lehrerin tätig.
        <br />
        {/* Die Möglichkeit, Sprache und Kultur ihres Heimatlandes zu vermitteln, stellen für sie eine stetige Motivation dar. */}
      </div>
    </div>
  );
}

export default Lehrperson;
