import React from 'react';
import './section.css';
import { cssFactory, siteCss } from '../common/css';
import ModuleLink from '../components/Links/ModuleLink';

const css = cssFactory('section');

function Kurse(props) {
  return (
    <div>
      <div className={css('heading')}>
        Kurse
      </div>
      <div className={siteCss('par')}>
        Die Kurse sind folgendermaßen gestaltet:
        <div className={siteCss('as-list')}>
          <div className={siteCss('as-list-item')}>10 Lektionen</div>
          <div className={siteCss('as-list-item')}>à 45 Minuten</div>
          <div className={siteCss('as-list-item')}>1 Mal pro Woche</div>
          <div className={siteCss('as-list-item')}>höchstens 5 Teilnehmer</div>
        </div>
      </div>
      <div className={siteCss('par')}>
        Die Kosten für den gesamten Kurs betragen:
        <div className={siteCss('table', 'last-right-aligned', 'as-list')}>
          <div className={siteCss('trow')}>
            <div className={siteCss('tcell')}>bei 1 Teilnehmer</div>
            <div className={siteCss('tcell')}>300 €</div>
          </div>
          <div className={siteCss('trow')}>
            <div className={siteCss('tcell')}>bei 2 Teilnehmern</div>
            <div className={siteCss('tcell')}>150 €</div>
          </div>
          <div className={siteCss('trow')}>
            <div className={siteCss('tcell')}>bei 3 Teilnehmern</div>
            <div className={siteCss('tcell')}>100 €</div>
          </div>
          <div className={siteCss('trow')}>
            <div className={siteCss('tcell')}>bei 4 Teilnehmern</div>
            <div className={siteCss('tcell')}>75 €</div>
          </div>
          <div className={siteCss('trow')}>
            <div className={siteCss('tcell')}>bei 5 Teilnehmern</div>
            <div className={siteCss('tcell')}>60 €</div>
          </div>
        </div>
      </div>
      {/* <div className={siteCss('par')}>
        Die Kurse finden an folgenden Terminen statt:
      </div> */}
      <div className={siteCss('par')}>
        Zum <ModuleLink moduleid={'Programm'} onFollow={props.onSwitch}>
          Programm
        </ModuleLink>
      </div>
    </div>
  );
}

export default Kurse;
