import React from 'react';
import './section.css';
import { cssFactory, siteCss } from '../common/css';

import { addresse, telefon, emailLegal, nameLegal, nameProgrammingLegal } from '../site/data';
import ModuleLink from '../components/Links/ModuleLink';

const css = cssFactory('section');

